<template>
  <div>
    <v-container class="admin-charlas__container">
      <v-row>
        <v-col>
          <show-dialog-charla
            v-bind="{ value }"
            @procesar="procesar($event)"
            @cerrar="cerrar()"
          />
          <base-material-card
            color="info"
            icon="mdi-view-carousel"
            inline
            title="charlas"
            class="px-5 py-3 mb-5"
          >
            <v-row class="admin-charlas_row__nuevo">
              <v-col class="col-12 col-md-6 text-left">
                <v-text-field
                  v-model="search"
                  label="Buscar por Título"
                  filled
                  color="info"
                />
              </v-col>
              <v-col class="col-12 col-md-6 text-right">
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      class="info ma-0"
                      v-bind="attrs"
                      @click="abrirCrear()"
                      v-on="on"
                    >
                      Nueva charla &nbsp; <v-icon>mdi-post</v-icon>
                    </v-btn>
                  </template>
                  <span>Crea una nueva charla</span>
                </v-tooltip>
              </v-col>
            </v-row>
            <v-simple-table class="admin-charlas__table">
              <thead>
                <tr>
                  <th>Codigo</th>
                  <th>Título</th>
                  <th>Relator</th>
                  <th>Sucursal</th>
                  <th style="text-align:right;">
                    Acciones
                  </th>
                </tr>
              </thead>

              <tbody>
                <tr
                  v-for="(item, i) in charlas"
                  :key="i"
                >
                  <td>{{ item.CodCharla }}</td>
                  <td>{{ item.Titulo }}</td>
                  <td>{{ item.Relator }}</td>
                  <td>{{ item.Sucursal }}</td>
                  <td class="text-right">
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          class="info px-2 ml-1 btn-edit"
                          v-bind="attrs"
                          small
                          v-on="on"
                          @click="abrirEditar(item.CodCharla)"
                        >
                          <v-icon>mdi-pencil</v-icon>
                        </v-btn>
                      </template>
                      <span>Editar</span>
                    </v-tooltip>
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          class="info px-2 ml-1 btn-delete"
                          v-bind="attrs"
                          small
                          v-on="on"
                          @click="eliminar(item.CodCharla, item.Titulo)"
                        >
                          <v-icon> mdi-trash-can </v-icon>
                        </v-btn>
                      </template>
                      <span>Eliminar</span>
                    </v-tooltip>
                  </td>
                </tr>
              </tbody>
            </v-simple-table>
            <v-pagination
              v-model="pagina"
              :length="Number(totalPaginas)"
              prev-icon="mdi-menu-left"
              next-icon="mdi-menu-right"
              previous-aria-label="ps"
              next-aria-label="sd"
              color="info"
            />
          </base-material-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
  import Vue from 'vue'
  import { mapState, mapMutations } from 'vuex'
  import swalAlertError from '@/mixins'
  export default {
    components: {
      ShowDialogCharla: () => import('@/components/base/ShowDialogCharla'),
    },
    mixins: [swalAlertError],
    data () {
      return {
        value: {
          active: false,
          charla: {
            CodCharla: '',
            Titulo: '',
            Imagen: '',
            Introduccion: '',
            Dirigido: '',
            Relator: '',
            Duracion: '',
            Sucursal: '',
            Orden: '',
            InMenu: '',
            ToLink: '',
            Modalidad: '',
            Lugar: '',
            Valor: '',
            Fecha: '',
            Hora: '',
            ImagenMiniatura: '',
            MetaTitle: '',
            MetaTags: 0,
          },
          crear: true,
          imagen: null,
          ImagenMiniatura: null,
          loader: false,
          metaTagBox: {
            name: '',
            content: '',
          },
          objetivos: [],
          temas: [],
        },
        charlas: '',
        totalcharlas: '',
        totalPaginas: '',
        pagina: 1,
        limite: 5,
        search: '',
      }
    },
    computed: {
      ...mapState(['token', 'usuarioAdmin']),
    },
    watch: {
      pagina: function () {
        // console.log(this.pagina)
        if (this.search) {
          this.getCharlas(this.pagina, this.search)
        } else {
          this.getCharlas(this.pagina, '')
        }
      },
      search: function () {
        // console.log(this.search)
        this.pagina = 1 // Seteamos la página en 1 por si pagineó antes
        if (this.search) {
          this.getCharlas(this.pagina, this.search)
        } else {
          this.getCharlas(this.pagina, '')
        }
      },
    },
    created () {
      this.getCharlas(this.pagina)
    },
    methods: {
      ...mapMutations(['cerrarSesion']),
      getCharlas (pagina, byTitleParam = '') {
        const limite = this.limite
        const desde = (pagina - 1) * this.limite
        const bytitle = byTitleParam.split(' ').filter(a => a)
        const body = {
          desde,
          limite,
          bytitle,
          fromAdmin: true,
        }
        Vue.axios
          .post('/api/charlas', body)
          .then(res => {
            this.charlas = res.data.result
            this.totalcharlas = res.data.total
            this.totalPaginas = Math.round(this.totalcharlas / this.limite)
          })
          .catch(e => {
            console.log(e.response)
          })
      },
      getCharla (id) {
        const body = {
          id,
          fromAdmin: true,
        }
        Vue.axios
          .post('/api/charlas/charla/', body)
          .then(res => {
            this.value.charla = res.data.result[0]
          })
          .catch(e => {
            console.log(e.response)
          })
      },
      getCharlaObjetivos (id) {
        const body = {
          id,
          fromAdmin: true,
        }
        Vue.axios
          .post('/api/charlas/objetivos', body)
          .then(response => {
            this.value.objetivos = response.data.objetivos
          })
          .catch(err => {
            console.log(err)
          })
      },
      getCharlaTemas (id) {
        const body = {
          id,
          fromAdmin: true,
        }
        Vue.axios
          .post('/api/charlas/temas', body)
          .then(response => {
            this.value.temas = response.data.temas
            this.charged = true
          })
          .catch(err => {
            console.log(err)
          })
      },
      abrirEditar (id) {
        this.getCharla(id)
        this.getCharlaObjetivos(id)
        this.getCharlaTemas(id)
        this.value.active = true
      },
      abrirCrear () {
        this.getCharlaObjetivos()
        this.getCharlaTemas()
        this.value.active = true
      },
      cerrar () {
        this.value.active = false
        this.value.charla = {
          CodCharla: '',
          Titulo: '',
          Imagen: '',
          Introduccion: '',
          Dirigido: '',
          Relator: '',
          Duracion: '',
          Sucursal: '',
          Orden: '',
          InMenu: '',
          ToLink: '',
          Modalidad: '',
          Lugar: '',
          Valor: '',
          Fecha: '',
          Hora: '',
          ImagenMiniatura: '',
          MetaTitle: '',
          MetaTags: 0,
        }

        this.value.imagen = null
        this.value.imagenMiniatura = null
        this.value.loader = false
        this.value.metaTagBox.name = ''
        this.value.metaTagBox.content = ''
        this.value.crear = true
      },
      procesar (val) {
        // console.log('processing...')
        this.value.loader = true
        const headers = {
          'x-token': this.token,
        }
        const body = new FormData()
        body.append('CodCharla', val.charla.CodCharla)
        body.append('Titulo', val.charla.Titulo)
        body.append('Imagen', val.imagen)
        body.append('Introduccion', val.charla.Introduccion)
        body.append('Dirigido', val.charla.Dirigido)
        body.append('Relator', val.charla.Relator)
        body.append('Duracion', val.charla.Duracion)
        body.append('Sucursal', val.charla.Sucursal)
        body.append('InMenu', val.charla.InMenu)
        body.append('ToLink', val.charla.ToLink)
        body.append('Modalidad', val.charla.Modalidad)
        body.append('Lugar', val.charla.Lugar)
        body.append('Valor', val.charla.Valor)
        body.append('Fecha', val.charla.Fecha)
        body.append('Hora', val.charla.Hora)
        body.append('ImagenMiniatura', val.imagenMiniatura)
        body.append('MetaTitle', val.charla.MetaTitle)
        body.append(
          'MetaTags',
          val.charla.MetaTags !== 0 ? JSON.stringify(val.charla.MetaTags) : 0,
        ) // Lo pasamos a cadena para después parsearlo en servidor
        body.append('Objetivos', JSON.stringify(val.objetivos))
        body.append('Temas', JSON.stringify(val.temas))
        body.append('UsuarioCreacion', this.usuarioAdmin.CodUsuario)
        body.append('UsuarioModificacion', this.usuarioAdmin.CodUsuario)

        if (val.charla.CodCharla) {
          this.update(body, headers)
        } else {
          this.create(body, headers)
        }
      },
      create (body, headers) {
        // console.log('create')
        Vue.axios
          .post('/api/charlas/crear', body, { headers })
          .then(res => {
            this.charlas.unshift(res.data.charla[0])
            this.cerrar()
            Vue.swal('Charla Creada', '', 'success')
          })
          .catch(e => {
            this.cerrar()
            this.swalAlertError(e)
          })
      },
      update (body, headers) {
        Vue.axios
          .put('/api/charlas/editar', body, { headers })
          .then(res => {
            console.log(res)
            const index = this.charlas.findIndex(
              charla => charla.CodCharla === Number(res.data.charla[0].CodCharla),
            )
            this.charlas[index].Titulo = res.data.charla[0].Titulo
            this.charlas[index].Relator = res.data.charla[0].Relator
            this.charlas[index].Sucursal = res.data.charla[0].Sucursal
            this.cerrar()
            Vue.swal('Charla Actualizada', '', 'success')
          })
          .catch(e => {
            this.cerrar()
            this.swalAlertError(e)
          })
      },
      eliminar (id, titulo) {
        Vue.swal({
          title: `Estás seguro que deseas borrar ${titulo}?`,
          showConfirmButton: true,
          showCancelButton: true,
          confirmButtonText: 'Eliminar',
          cancelButtonText: 'Cancelar',
          confirmButtonColor: '#ff5252',
          cancelButtonColor: '#a1a1a1',
        }).then(result => {
          if (result.isConfirmed) {
            Vue.axios
              .delete(`/api/charlas/${id}`, {
                headers: { 'x-token': this.token },
              })
              .then(res => {
                const index = this.charlas.findIndex(
                  n => n.CodCharla === Number(res.data.CodCharla),
                )
                this.charlas.splice(index, 1)
                Vue.swal('charla Eliminada', '', 'success')
              })
              .catch(e => {
                this.cerrar()
                this.swalAlertError(e)
              })
          }
        })
      },
    },
  }
</script>
